body, button, input, select, textarea, h1, h2, h3, h4, h5, h6, p {
  font-family: Inter, sans-serif;
}

html, body {
  width: 100%;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}

body {
  background-color: var(--md-sys-color-background);
  color: var(--md-sys-color-on-background);
  box-sizing: border-box;
  font-family: Inter, sans-serif;
}

*, :before, :after {
  box-sizing: inherit;
}

.navbar {
  background-color: var(--md-sys-color-surface);
  color: var(--md-sys-color-on-surface);
  z-index: 1000;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 16px;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.navbar-left {
  align-items: center;
  gap: 16px;
  display: flex;
}

.logo-link {
  text-decoration: none;
}

.logo-image {
  width: auto;
  height: 50px;
}

#menu-button {
  color: var(--md-sys-color-on-surface);
  display: none;
}

#toggle-theme md-icon {
  color: var(--md-sys-color-on-surface);
}

.navbar-links, .navbar-right {
  align-items: center;
  gap: 8px;
  display: flex;
}

.nav-button {
  color: var(--md-sys-color-primary);
  cursor: pointer;
  background-color: #0000;
  border-radius: 4px;
  padding: 8px 16px;
  font-size: 1.1rem;
  font-weight: 550;
}

.nav-button:hover, .nav-button.active {
  background-color: #e8c25d1a;
}

.main-content {
  flex-direction: column;
  align-items: center;
  width: 100%;
  padding-top: 100px;
  display: flex;
}

.section {
  width: 80%;
  max-width: 800px;
  margin: 16px auto;
  padding: 16px;
  display: none;
}

.section.active {
  display: block;
}

.card {
  background-color: var(--md-sys-color-surface-container);
  color: var(--md-sys-color-on-surface);
  border-radius: 12px;
  box-shadow: 0 1px 3px #0003, 0 1px 2px #00000024, 0 2px 1px -1px #0000001f;
}

.board {
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 16px;
  display: grid;
}

.board-member {
  text-align: center;
  background-color: var(--md-sys-color-surface);
  border-radius: 8px;
  padding: 16px;
}

@media (width <= 768px) {
  .navbar-links {
    display: none;
  }

  #menu-button {
    display: flex;
  }

  #nav-menu {
    display: block;
  }
}

@media (width <= 480px) {
  .section {
    width: 95%;
  }
}
